<template>
    <div class="app-content">
		<div class="container">
			<div class="page-title">
				<h1>Заказы</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="input-group">
					<span class="input-group-text">Поиск</span>
					<input type="text" class="form-control" placeholder="Номер заказа" v-model="query">
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<div class="alert alert-danger" role="alert" v-if="!orders.length">
					На данный момент, заказы отсутствуют =(
				</div>
				<div class="orders-table" v-if="!!orders.length">
					<table class="table table-hover border">
						<thead>
							<tr>
								<th>ID</th>
								<th>Время</th>
								<th>Товары (кол-во)</th>
								<th>Данные</th>
								<th>Сумма</th>
								<th>Статус</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(order, index) in filteredProducts[paginationSelectedPage]" :key="index">
								<td>{{ order.id }}</td>
								<td class="time">{{ order.date}}</td>
								<td class="items">
									<div class="item" v-for="(product, i) in order.products" :key="i" style="display: flex; align-items: center;">
										<span class="name">{{ product.name['ua'] }}</span>
										<span class="count badge bg-primary" style="margin-left: 5px;">{{ product.count }}</span>
									</div>
								</td>
								<td class="info">
									<p>Имя: <span>{{ order.name }}</span></p>
									<p>Телефон: <span>{{ order.phone }}</span></p>
									<p>Email: <span>{{ (!!order.email.length) ? order.email : 'ー' }}</span></p>
								</td>
								<td class="price"><span>{{ order.totalPrice }}</span> грн</td>
								<td class="status">
									<div class="form-check form-switch">
										<input class="form-check-input" type="checkbox" v-model="order.status.ready">
										<label class="form-check-label">Собран</label>
									</div>
									<div class="form-check form-switch">
										<input class="form-check-input" type="checkbox" v-model="order.status.send">
										<label class="form-check-label">Отправлен</label>
									</div>
									<div class="form-check form-switch">
										<input class="form-check-input" type="checkbox" v-model="order.status.done">
										<label class="form-check-label">Выполнен</label>
									</div>
									<div class="form-check form-switch">
										<input class="form-check-input" type="checkbox" v-model="order.status.payed">
										<label class="form-check-label">Оплачен</label>
									</div>
									<div class="note">
										<textarea class="form-control" v-model="order.note" placeholder="Примечание"></textarea>
									</div>
								</td>
							</tr>
						</tbody>
					</table>
                    <nav>
                        <ul class="pagination justify-content-end">
                            <li class="page-item" :class="{'disabled': paginationSelectedPage == 0}"><a class="page-link" @click="paginationController(paginationSelectedPage - 1)">Назад</a></li>

                            <li class="page-item" :class="{'active': (index == paginationSelectedPage)}"  v-for="(page, index) in filteredProducts" :key="index" @click="paginationController(index)"><a class="page-link">{{ index + 1 }}</a></li>

                            <li class="page-item" :class="{'disabled': paginationSelectedPage == filteredProducts.length - 1}"><a class="page-link" @click="paginationController(paginationSelectedPage + 1)">Вперед</a></li>
                        </ul>
                    </nav>
				</div>
			</div>
		</div>
	</div>
</template>

<script>


export default {
    data() {
        return {
            orders: [],
            saveInProgress: false,
            isSavedSuccess: false,
            query: '',
            paginationSelectedPage: 0,
            productsPerPage: 12
        }
    },  
    components: {

    },
    computed: {
        ordersInfo() {
            return this.$store.getters['getOrders'];
        },
        filteredProducts() {
            var index = 0;
            var result = [[]];
            var orders = (!this.query.length)
                ? this.orders
                : this.orders.filter((order) => {
                    return ~order.id.indexOf(this.query.toLowerCase())
                });

            for (var i = 0; i < orders.length; i++) {
                if (i % this.productsPerPage == 0 && i != 0) {
                    index++;
                    result[index] = [];
                }
                result[index].push(orders[i]);
            }
            return result;
        }
    },
    watch: {
        ordersInfo(newValue) {
            this.orders = newValue;
        }
    },
    methods: {
        savePage() {
            this.saveInProgress = true;
            this.$store.dispatch('saveOrdersPage', this.orders)
                .then(() => {
                    this.saveInProgress = false;
                    this.isSavedSuccess = true;
                    setTimeout(() => {
                        this.isSavedSuccess = false;
                    }, 3000);
                });
        },
        paginationController(index) {
            this.paginationSelectedPage = index;
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
        }
    },
    created() {
        this.orders = this.ordersInfo;
    }
}
</script>
